import { Component, OnInit } from '@angular/core';
import { FilterService, Message, MessageService, PrimeNGConfig, TreeNode } from 'primeng/api';
import cdata from '../assets/sample.json';

interface Info {
  index: number;
  name: string;
  star: number;
  basejob: string;
  element: string;
  class: string;
  s1: number;
  s2: number;
  s3: number;
  s4: number;
  s5: number;
  s6: number;
  s7: number;
  s8: number;
}

interface BaseJob {
  name?: string;
  image?: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  providers: [MessageService]
})
export class AppComponent implements OnInit {
  msgs: Message[] = [];
  info: Info[] = [];
  basejobs: BaseJob[] = [];
  selectbasejob: BaseJob[] = [];
  location1!: Info;
  location2!: Info;
  location3!: Info;
  location4!: Info;
  location5!: Info;
  location6!: Info;
  location7!: Info;
  location8!: Info;
  s1 = 0;
  s2 = 0;
  s3 = 0;
  s4 = 0;
  s5 = 0;
  s6 = 0;
  s7 = 0;
  s8 = 0;
  total = 0;
  value1 = 0;
  value2 = 0;
  value3 = 0;
  value4 = 0;
  value5 = 0;
  value6 = 0;
  value7 = 0;
  value8 = 0;

  constructor(private messageService: MessageService, private primengConfig: PrimeNGConfig, private filterService: FilterService) { }
  ngOnInit(): void {
    this.info = cdata;
    this.location1 = this.info[0];
    this.location2 = this.info[0];
    this.location3 = this.info[0];
    this.location4 = this.info[0];
    this.location5 = this.info[0];
    this.location6 = this.info[0];
    this.location7 = this.info[0];
    this.location8 = this.info[0];
    this.basejobs = [
      { name: '劍士', image: '../favicon.ico' },
      { name: '弓箭手', image: './favicon.ico' },
      { name: '盜賊', image: './favicon.ico' },
      { name: '巫師', image: './favicon.ico' },
      { name: '祭司', image: './favicon.ico' },
      { name: '戰士', image: './favicon.ico' },
      { name: '槍術士', image: './favicon.ico' },
      { name: '精靈', image: './favicon.ico' },
    ];
  }

  show(): void {
    console.log(this.location1);
    console.log(this.location2);
    console.log(this.location3);
    console.log(this.location4);
    console.log(this.location5);
    console.log(this.location6);
    console.log(this.location7);
    console.log(this.location8);
  }
  caulcu(): void {
    this.s1 = this.location1.s1 + this.location2.s1 + this.location3.s1 + this.location4.s1 + this.location5.s1 + this.location6.s1 + this.location7.s1 + this.location8.s1;
    this.s2 = this.location1.s2 + this.location2.s2 + this.location3.s2 + this.location4.s2 + this.location5.s2 + this.location6.s2 + this.location7.s2 + this.location8.s2;
    this.s3 = this.location1.s3 + this.location2.s3 + this.location3.s3 + this.location4.s3 + this.location5.s3 + this.location6.s3 + this.location7.s3 + this.location8.s3;
    this.s4 = this.location1.s4 + this.location2.s4 + this.location3.s4 + this.location4.s4 + this.location5.s4 + this.location6.s4 + this.location7.s4 + this.location8.s4;
    this.s5 = this.location1.s5 + this.location2.s5 + this.location3.s5 + this.location4.s5 + this.location5.s5 + this.location6.s5 + this.location7.s5 + this.location8.s5;
    this.s6 = this.location1.s6 + this.location2.s6 + this.location3.s6 + this.location4.s6 + this.location5.s6 + this.location6.s6 + this.location7.s6 + this.location8.s6;
    this.s7 = this.location1.s7 + this.location2.s7 + this.location3.s7 + this.location4.s7 + this.location5.s7 + this.location6.s7 + this.location7.s7 + this.location8.s7;
    this.s8 = this.location1.s8 + this.location2.s8 + this.location3.s8 + this.location4.s8 + this.location5.s8 + this.location6.s8 + this.location7.s8 + this.location8.s8;
    this.total = this.s1 + this.s2 + this.s3 + this.s4 + this.s5 + this.s6 + this.s7 + this.s8;
  }

  onChange(event: any): void {
    this.caulcu();
  }
}
